/* from scratch */
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

/* common styles */
body {
  margin: 0;
  padding: 0;
  color: #122d4b;
  font-family: 'Helvetica', 'Helvetica Neue', -apple-system, Arial, sans-serif;
  font-size: 16px;
  letter-spacing: .3px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

header,
footer {
  background-color: #122d4b;
  color: #fff; }

header {
  position: relative;
  z-index: 1;
  box-shadow: 0 2px 22px rgba(69, 69, 69, 0.4); }

.main {
  background-color: #f6f6f5;
  background-size: cover;
  background-image: url(/YeNotes-test/build/static/media/text-foto.704f837b.png); }

.heading {
  margin-bottom: 24px; }

.row {
  display: -webkit-flex;
  display: flex; }

.side button {
  width: 100%;
  margin-bottom: 8px;
  text-align: left; }

/* typography */
button {
  border: none; }

a {
  display: -webkit-inline-flex;
  display: inline-flex;
  color: inherit;
  text-decoration: none; }

input {
  border-radius: 2px;
  border: none;
  padding: 8px 15px; }

table {
  width: 100%;
  background-color: #fff;
  box-shadow: 0 0 7px rgba(113, 113, 113, 0.36);
  border-collapse: collapse;
  border-radius: 4px; }

td {
  padding: 15px; }
  td:first-child {
    padding-left: 30px;
    font-weight: bold;
    color: #dc5162; }
  td:not(:first-child) {
    width: 40%; }
  td:last-child {
    padding-right: 30px; }

tr:nth-child(2n-1) td {
  background-color: #efefef; }

th {
  padding: 15px;
  text-align: left;
  font-family: "HelveticaBold", Arial, sans-serif; }
  th:first-child {
    padding-left: 30px; }
  th:last-child {
    padding-right: 30px; }

@media (max-width: 450px) {
  td:first-child,
  th:first-child {
    padding-left: 15px; }
  td:last-child,
  th:last-child {
    padding-right: 15px; } }

@media (max-width: 400px) {
  tr {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column; }
  td,
  th {
    width: auto !important;
    padding-top: 5px;
    padding-bottom: 5px; }
    td:first-child,
    th:first-child {
      padding-top: 15px; }
    td:last-child,
    th:last-child {
      padding-bottom: 15px; } }

