/* fonts */
/* colors */
button {
  border: none;
  cursor: pointer; }

.btn {
  font-size: 14px;
  white-space: nowrap; }
  .btn-secondary {
    background-color: white;
    box-shadow: 0 0 7px rgba(113, 113, 113, 0.36);
    font-family: "HelveticaBold", Arial, sans-serif;
    text-shadow: 0 2px 3px rgba(0, 0, 0, 0.2); }
  .btn-primary {
    background-color: #e4bd7d;
    color: #fff;
    box-shadow: 0 0 7px rgba(113, 113, 113, 0.36);
    font-family: "HelveticaBold", Arial, sans-serif;
    text-shadow: 0 2px 3px rgba(0, 0, 0, 0.2); }
  .btn-success {
    background-color: #dc5162;
    color: #fff;
    box-shadow: 0 0 7px rgba(113, 113, 113, 0.36);
    font-family: "HelveticaBold", Arial, sans-serif;
    text-shadow: 0 2px 3px rgba(0, 0, 0, 0.2); }
  .btn-icon {
    background-color: transparent;
    box-shadow: none; }
  .btn-sm {
    height: 32px;
    padding: 8px 12px;
    border-radius: 2px; }
  .btn-md {
    height: 36px;
    padding: 10px 15px;
    border-radius: 4px; }

/* fonts */
/* colors */
/* header */
.row-header {
  height: 70px;
  font-size: 14px;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
  -webkit-justify-content: space-around;
          justify-content: space-around; }

.col-menu {
  -webkit-flex-grow: 0;
          flex-grow: 0; }

.fa-equals {
  font-size: 32px; }

.col-logo {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center; }

.logo {
  width: 34px;
  height: 34px; }

.logo-text {
  font-family: 'Playfair Display', 'Georgia', serif;
  font-size: 25px;
  white-space: nowrap;
  margin-left: 15px; }

.search,
.translate {
  position: relative;
  font-family: 'Roboto', sans-serif;
  height: 32px; }
  .search input,
  .translate input {
    width: 100%;
    height: 100%;
    padding: 5px 12px;
    padding-right: 30px;
    color: #122d4b; }
  .search ::-webkit-search-cancel-button,
  .translate ::-webkit-search-cancel-button {
    position: relative;
    right: 5px; }
  .search button,
  .translate button {
    position: absolute;
    right: 5px;
    height: 100%; }
    .search button:focus,
    .translate button:focus {
      outline: none; }
  .search .fa-search,
  .translate .fa-search {
    color: #e4bd7d;
    font-size: 20px; }
  .search .search-icon,
  .translate .search-icon {
    margin-top: 3px; }

.col-translate {
  margin-right: auto; }

.translate {
  width: 100%; }
  .translate input {
    font-weight: bold;
    padding-left: 40px;
    padding-right: 30px; }
  .translate i.fa-exchange-alt {
    position: absolute;
    left: 10px;
    top: 8px;
    color: #1f476b; }
  .translate button {
    top: -1px;
    right: 5px;
    color: white;
    text-shadow: 0 2px #122d4b; }

.col-user button {
  width: 100%; }

.language {
